.thebox
{
	width:90%;
	height:100px;
	background-color: yellow;
	margin-top:2px;
}

.theheader
{
	width:100%;
	height:30px;
	background-color: blue;
	float:left;
}
.themessage
{
	width:90%;
	height:70px;
	background-color: green;
	float:left;
}

.thedelete
{
	width:10%;
	height:70px;
	float:right;
	background-color: red;
}

ul
{
	list-style:none;
}

li{
	height:100px;
	width:70%;
	margin-left:auto;
	margin-right:auto;
	margin-top:4px;
	background-color:#e1e8ee;
	//border-radius:50px 50px 50px 50px;
	
}

li div
{
	width:95%;
	height:100%;
	background-color:#e1e8ee;
	float:left;
	display:flex;
	justify-content: center;
	
}

.deletebutton
{
	height:21%;
	width:3%;
	float:right;
    margin-top:10px;
	margin-right:5px;
	filter:grayscale(50%);
	
}
.deletebutton:hover
{
	cursor:pointer;
}
input
{
	height:60px;
	width:94%;
	margin-left:auto;
	margin-right:auto;
	color:#ffff;
	background-color: #0d1a00;
}
button
{
	background-color:#80ff00;
	//border:1px solid #bfff80;
	width:5%;
	height:60px;
	text-decoration:none;
	margin-left:auto;
	margin-right:auto;
}
button:hover
{
	cursor:pointer;
}